.movie__detail {
  background-color: white;
  margin-bottom: 70px;
  display: flex;
  font-weight: 300;
  padding: 20px;
  margin-left: 50px;
  margin-right: 50px;
  border-radius: 5px;
  color: #adaeb9;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25), 0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.detailbox1{
  flex:2;    
  border: 3px solid #222; 
  margin: 10px;
  padding: 20px;
  background-color: rgba(0, 0, 0, 1);
}
.detailbox2{
  flex:0.7;      
  /* border: 3px solid #222;     */
  margin: 10px;
  display: flex;
  flex-direction: column;
  font-weight: 600;
  color: #adaeb9;
}

.detailbox3{
  flex:3;  
  /* border: 3px solid #222;     */
  margin: 10px;    
  display: flex;
  flex-direction: column;
  color: black;
}

.movie__detail img{
  position: relative;
  float: left;
  top: 50px;
  width: 100%;
  height: 90%;
  box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25), 0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}


.movie__info__title{
  flex: 1;
  font-weight: 600;
  text-decoration: none;
  color: black;
  font-size: 2em;
  /* border: 1px solid #222; */
  margin-bottom: 30px;  
  padding : 20px;
}

.movie__info__year {
  flex: 1;
  /* border: 1px solid #222; */
  margin-bottom: 30px;  
  padding : 20px;
}

.movie__info__summary {
  flex: 5;
  /* border: 1px solid #222; */
  margin-bottom: 30px;  
  padding : 20px;
}

.movie__info__rating {
  flex: 1;
  /* border: 1px solid #222; */
  margin-bottom: 30px;  
  padding : 20px;
}

.movie__info__runtime {
  flex: 1;
  /* border: 1px solid #222; */
  margin-bottom: 30px;  
  padding : 20px;
}

.movie__info__genres {
  flex: 1;
  /* border: 1px solid #222; */
  margin-bottom: 30px;  
  padding : 20px;
  display: flex;
  flex-wrap: wrap;
}

.movie__genres {
  list-style: none;
  padding: 0;
  margin-right: 15px;
  color: black;
}

.movie__genres li {
  margin-right: 10px;
}

.movie__info__title span{
  color: black;
  font-weight: 600;
  font-size: 0.5em;
  
}




#container {
  width:1000px;
  margin:20px auto;
}
h1 {
  text-align:center;
}
.prod-list {
  list-style:none;
  padding:0;
}
.prod-list li {
  float: left;
  padding: 0;
  margin: 10px;
  overflow: hidden;
}
.prod-list img {
  margin:0;
  padding:0;
  float:left;
  z-index:5;
  width: 300px;
  height: 200px;
} 

.prod-list .caption{
  position: absolute;
  top: 200px;
  width: 300px;
  height: 200px;
  padding-top: 20px;
  background: rgba(0,0,0,0.6);
  opacity: 0;
  transition: all 0.6s ease-in-out;
  z-index: 10;
}

.prod-list li {
  float: left;
  padding: 0;
  margin: 10px;
  overflow: hidden;
  position: relative;
}

.prod-list li:hover .caption{
  opacity: 1;
  transform: translateY(-200px);
}

.prod-list .caption h2, .prod-list .caption p {
  color: #fff;
  text-align: center;
}
