/* 메뉴 버튼 */

.button_menu {
  padding: 10px 20px;
  text-align: center;
  margin-top: 20px;
  font-size: 0.4em;
  
}

.button_menu:before{
  content:'';
 
  display:inline-block;
  vertical-align:middle;
}
.button_menu button{
  background:#2c3e50;
  color:#fff;
  border:none;
  position:relative;
  height:60px;
  width: 120px;
  font-size:1.6em;
  padding:0 2em;
  cursor:pointer;
  transition:800ms ease all;
  outline:none;
}
.button_menu button:hover{
  background:#fff;
  color:#2c3e50;
}
.button_menu button:before,button:after{
  content:'';
  position:absolute;
  top:0;
  right:0;
  height:2px;
  width:0;
  background: #2c3e50;
  transition:400ms ease all;
}
.button_menu button:after{
  right:inherit;
  top:inherit;
  left:0;
  bottom:0;
}
.button_menu button:hover:before,button:hover:after{
  width:100%;
  transition:800ms ease all;
}




